@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  width: 100vw;
  margin: 0;
  font-family: 'Lato', sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #191919 !important;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
body {
    background-color: #191919;
    width: 100vw;
    height: 100vh;
    display: flex;
}

.container {
    position: fixed;
    display: flex;
    width: 100vw;
    height: 100vh;
    align-items: center;
    flex-direction: column;
    z-index: 5;
    margin: auto auto;
}

.button {
    margin: 50px auto auto auto;
    width: 120px;
    height: 40px;
    border-width: 0px;
    border-radius: 3px;
    background-color: #1ED761;
    cursor: pointer;
    font-weight: bold;
    font-size: 15px;
    color: #191919;
    outline: none;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}

.button-loading {
    margin: 50px auto auto auto;
    padding-left: 20px;
    padding-right: 20px;
    height: 40px;
    border-width: 0px;
    border-radius: 3px;
    background-color: #d71e1e;
    cursor: pointer;
    font-weight: bold;
    font-size: 15px;
    color: #191919;
    outline: none;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
    pointer-events: none;
}

@-webkit-keyframes loading {
    0% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.1) }
    10% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.3) }
    20% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.5) }
    30% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.7) }
    40% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.9) }
    50% { box-shadow: 0 0px 50px rgba(223, 1, 1, 1) }
    60% { box-shadow: 0 0px 50px rgba(223, 1, 1, 1) }
    30% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.9) }
    70% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.7) }
    80% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.5) }
    90% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.3) }
    100% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.1) }
}

@keyframes loading {
    0% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.1) }
    10% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.3) }
    20% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.5) }
    30% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.7) }
    40% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.9) }
    50% { box-shadow: 0 0px 50px rgba(223, 1, 1, 1) }
    60% { box-shadow: 0 0px 50px rgba(223, 1, 1, 1) }
    30% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.9) }
    70% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.7) }
    80% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.5) }
    90% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.3) }
    100% { box-shadow: 0 0px 50px rgba(223, 1, 1, 0.1) }
}

.button-connect {
    margin: 50px auto auto auto;
    width: 120px;
    height: 40px;
    border-width: 0px;
    border-radius: 3px;
    background-color: #ffffff;
    cursor: pointer;
    font-weight: bold;
    font-size: 15px;
    color: #191919;
    outline: none;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
    pointer-events: none;
}

@-webkit-keyframes connecting {
    0% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.1) }
    10% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.3) }
    20% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.5) }
    30% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.7) }
    40% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.9) }
    50% { box-shadow: 0 0px 50px rgba(255, 255, 255, 1) }
    60% { box-shadow: 0 0px 50px rgba(255, 255, 255, 1) }
    30% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.9) }
    70% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.7) }
    80% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.5) }
    90% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.3) }
    100% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.1) }
}

@keyframes connecting {
    0% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.1) }
    10% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.3) }
    20% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.5) }
    30% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.7) }
    40% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.9) }
    50% { box-shadow: 0 0px 50px rgba(255, 255, 255, 1) }
    60% { box-shadow: 0 0px 50px rgba(255, 255, 255, 1) }
    30% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.9) }
    70% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.7) }
    80% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.5) }
    90% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.3) }
    100% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.1) }
}

button:hover {
    background-color: white;
    color: #191919;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}

img {
    margin: auto auto 0px auto;
    width: 140px;
}

.particles {
    position: fixed;
    opacity: 0.8;
}
.sidebar {
    width: 100%;
    background-color: #191919;
    height: 87vh;
    position: absolute;
    overflow-y: scroll;
}

.playlist-title {
    font-size: 13px; 
    color: #b6b6b6;
    cursor: default;
    letter-spacing: 2px;
    margin-left: 20px;
}

.playlist-name {
    font-size: 13px; 
    color: #b6b6b6;
    cursor: default;
    margin-left: 20px;
    padding-left: 0px;
}
.playlist-name:hover {
    color: white;
}

.library-title {
    font-size: 13px; 
    color: #b6b6b6;
    cursor: default;
    letter-spacing: 2px;
    margin-left: 20px;
}
.library-list {
    list-style: none;
    margin-bottom: 30px;
}
.library-list ul {
    font-size: 14px;
    font-weight: bolder; 
    color: #b6b6b6;
    cursor: default;
    margin-left: 20px;
    padding-left: 0px;
}
.library-list ul:hover {
    color: white;
}

.selector {
    display: flex;
    flex-direction: row;
    opacity: 0.6;
    margin-left: 20px;
    margin-bottom: 30px;
    margin-top: 20px;
}
.selector:hover {
    opacity: 1;
}
.selector img {
    width: 25px;
    height: 25px;
    margin: 0px;
    padding: 0px;
}
.selector h1 {
    margin-left: 15px;
    font-size: 14px;
    font-weight: bolder; 
    color: white;
    cursor: default;
}

#selected {
    color: white;
    border-left: 5px solid rgb(24, 214, 94);
    margin-left: 0;
    padding-left: 15px;
    opacity: 1;
}
.navbar {
    width: 85vw;
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(30, 30, 30, 1)), to(rgba(30, 30, 30, 0.2)));
    background-image: linear-gradient(to bottom, rgb(30, 30, 30, 1), rgba(30, 30, 30, 0.2));
    height: 50px;
    position: fixed;
    align-items: center;
    display: flex;
    z-index: 5;
}
.avatar {
    width: 30px; 
    height: 30px;
    margin: auto 10px auto auto;
    border-radius: 100%;
}
.name {
    margin: auto 20px auto 0px;
    color: white;
    font-size: 14px;
}
.name:hover {
    color: white;
    text-decoration: underline;
    margin-right: 20px;
    cursor: default;
}
.arrow-down {
    width: 20px; 
    margin: auto 40px auto 0px;
    outline: none;
}

.cascade {
    background-color: #2f2f2f !important;
    width: auto !important;
    height: 35px !important;
    box-shadow: 0px 7px 10px rgb(19, 19, 19) !important;
}

.cascade ul {
    color: rgb(182, 182, 182) !important;
    cursor: pointer !important;
    list-style-type: none !important;
}

.cascade ul li {
    padding: 5px 15px !important;
    font-size: 14px !important;
}
.cascade ul li:hover{
    background-color: #494949;
    color: white;
    border-bottom-right-radius: 3px;
    border-bottom-left-radius: 3px;
}

.search-bar {
    width: 200px;
    margin: auto auto auto 20px;
}
.library {
    margin-top: 70px;
}
.playlist {
    display: flex;
    flex-direction: row;
    margin-top: 80px;
    margin-bottom: 30px;
}
.cover {
    width: 200px;
    height: 200px;
    padding: 0;
    margin: 0 0 0 30px;
    box-shadow: 0px 10px 15px #0e0e0e;
}
.subtitle {
    margin-top: 48px;
    margin-left: 30px; 
    margin-bottom: 0px;
    font-size: 13px; 
    color: white;
    cursor: default;
    letter-spacing: 2px;
}
.title {
    margin-left: 30px; 
    margin-bottom: 10px;
    color: white; 
    font-weight: bolder;
    font-size: 50px;
    line-height: 50px;
}
.author {
    margin-left: 30px; 
    margin-top: 0px;
    margin-bottom: 20px;
    font-size: 13px; 
    color: #b6b6b6;
    cursor: default;
}
.author strong {
    color: white;
}
.author strong:hover {
    border-bottom: solid 1px white;
}

.play-button {
    background-color: rgb(27, 172, 80);
    border-width: 0; 
    outline: none;
    cursor: default; 
    margin-left: 30px;
    margin-top: 0;
    color: white;
    font-size: 12px;
    letter-spacing: 2px;
    height: 35px;
    width: 110px;
    border-radius: 30px;
    margin-bottom: 20px;
}
.play-button:hover {
    background-color: rgb(24, 214, 94);
    color: white;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.table{
    border-color: transparent !important;
}
.row:hover{
    background-color: rgba(255, 255, 255, 0.1);
    
}
.column{
    border-color: rgba(255, 255, 255, 0.1) !important;
}
.columnTitle{
    font-size: 13px; 
    color: #b6b6b6;
    cursor: default;
    letter-spacing: 2px;
    cursor: pointer;
}
.columnTitle:hover{
    color: white;
}
.columnData{
    font-size: 13px;
    color: white;
    cursor: default;
}
.play-music{
    width: 25px;
    opacity: 0.6;
}
.play-music:hover{
    opacity: 1;
}
.calendar{
    width: 15px;
    opacity: 0.6;
    cursor: pointer;
}
.calendar:hover{
    opacity: 1;
}
.list .ant-table-small {
    border-width: 0;
}
.profile {
    display: flex;
    flex-direction: row;
    margin-top: 70px;
    margin-bottom: 30px;
}
.profile .cover {
    width: 120px;
    height: 120px;
    border-radius: 100%;
    padding: 0;
    margin: 0 0 0 50px;
    box-shadow: 0px 10px 15px #0e0e0e;
}
.profile .subtitle {
    margin-top: 0;
    margin-left: 20px; 
    margin-bottom: 0px;
    font-size: 13px; 
    color: white;
    cursor: default;
    letter-spacing: 2px;
}
.profile .title {
    margin-left: 20px; 
    margin-bottom: 10px;
    width: 500px;
    color: white; 
    font-weight: bolder;
    font-size: 40px;
    line-height: 50px;
}

.profile-options {
    padding-left: 0px;
    margin-left: 50px;
    letter-spacing: 2px;
    list-style: none;
    display: flex;
    flex-direction: row;
    font-size: 12px;
    font-weight: bolder; 
    color: #b6b6b6;
    cursor: default;
}
.profile-options ul {
    padding-left: 0px;
    margin-right: 50px;
}
.profile-options ul:hover {
    color: white;
}

.profile-options .selected {
    color: white;
    border-bottom: 2px solid #1ED761;
    padding-left: 0px;
}

.description {
    margin-top: 10px;
    display: flex;
    padding: 0;
    margin-left: 50px;
    color: white;
    align-items: center;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    width: 78vw;
}
.description .public-playlists {
    font-weight: bolder;
    font-size: 17px;
    padding-left: 0;
    cursor: default;
    flex: 1 1;
}
.description .see-all{
    font-size: 13px;
    color: #b6b6b6;
    letter-spacing: 2px;
    cursor: default;
    padding-left: 0px;
}
.description .see-all:hover{
    color: white;
    border-bottom: 1px solid white;
}

.playlists {
    display: grid;
    grid-template-columns: 16vw 16vw 16vw 16vw 16vw;
    margin-left: 50px;
}
.playlists .album {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
}
.playlists .album img {
    width: 180px;
    height: 180px;
    padding-left: 0;
    margin-left: 0;
}
.playlists .album img:hover {
    opacity: 0.7;
}
.playlists .album h1 {
    color: white;
    cursor: default;
    font-weight: bolder;
    margin-top: 5px;
    font-size: 14px;
    width: 180px
}
.playlists .album h1:hover {
    text-decoration: underline;
}
.dashboard {
    margin: auto 0 auto auto;
    position: absolute;
    margin-left: 15vw;
    overflow-y: scroll;
    width: 100%;
    background-color: #1E1E1E;
    height: 87vh;
}
.player {
    background-color: #2C2C2C;
    height: 100%;
    width: 100vw;
    box-shadow: 0px -5px 20px #2C2C2C;
    display: flex;
    align-items: center;
}

.song-info{
    display: flex;
}
.song-cover {
    width: 50px;
    height: 50px;
    margin: auto auto auto 20px;
}
.song-info li {
    list-style: none;
    padding-left: 0px;
    margin: auto 0;
    display: flex;
    flex-direction: column;
    justify-content: left;
    width: 25vw;
}
.song-name {
    font-size: 13px; 
    color: white;
    cursor: default;
    margin-left: 15px;
    padding-left: 0;
    margin-top: 0px;
    margin-bottom: 5px;
}
.song-name:hover {
    border-bottom: solid 1px white;
}
.song-artist {
    font-size: 13px; 
    color: white;
    opacity: 0.5;
    cursor: default;
    margin-top: auto;
    margin-left: 15px;
    padding-left: 0;
    margin-bottom: 0;
}
.song-artist:hover {
    opacity: 1;
    border-bottom: solid 1px white;
}

.song-player {
    width: 50vw;
    height: 100%;
    margin: auto auto auto 0;
    display: flex;
    flex-direction: column;
}

.player .buttons {
    margin: auto auto 5px auto;
    display: flex;
    align-items: center;
}

.player .progress-bar-container {
    margin: 0 auto auto auto;
    display: flex;
    align-items: center;
}

.player .progress-bar-container span {
    padding: 0 10px;
    color: rgba(255, 255, 255, 0.5);
    font-size: 13px;
}

.player .progress-bar-container .progress-bar {
    width: 37vw;
    height: 5px;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 5px;
    display: block;
}

.player .progress-bar-container .progress-bar div {
    background-color: rgba(255, 255, 255, 0.5);
    height: 100%;
    border-radius: 5px;
}
.player .progress-bar-container .progress-bar div:hover {
    background-color: rgb(24, 214, 94);
}

.player .play-pause {
    color: rgba(255, 255, 255, 0.5);
    font-size: 40px;
}
.player .play-pause:hover {
    color: rgba(255, 255, 255, 1);
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.player .next-previous {
    color: rgba(255, 255, 255, 0.5);
    font-size: 20px;
    margin: auto 15px;
}
.player .next-previous:hover {
    color: rgba(255, 255, 255, 1);
}

.song-volume {
    width: 25vw;
    height: 100%;
    display: flex;
    align-items: center;
    margin-right: 10px;
}

.song-volume .muted-desmuted {
    margin: auto 10px auto auto;
    color: rgba(255, 255, 255, 0.5);
    font-size: 20px;
}
.song-volume .muted-desmuted:hover {
    color: rgba(255, 255, 255, 1);
}

.song-volume .progress-bar-container {
    margin: auto auto auto 0;
    display: flex;
}
.song-volume .progress-bar-container .progress-bar {
    width: 100px;
    height: 5px;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 5px;
    display: block;
    margin: auto 0 auto auto;
}
.loading-modal{
    width: 100vw; 
    height: 100vh;
    display: flex;
    align-content: space-around !important;
    align-items: center !important;
}
